    <div class="footer_top" :class="{ hideFoot: !showFooter }">
      <ul>
        <li>
          <a href="https://www.facebook.com/gabriele.scarparo.3" target="blank">
            <i class="fab fa-facebook-f icon"></i>
          </a>
        </li>
        <li>
          <a href="https://twitter.com/gabrisca_85" target="blank"
            ><i class="fab fa-twitter icon"></i
          ></a>
        </li>
        <li>
          <a
            href="https://www.linkedin.com/in/gabriele-scarparo/"
            target="blank"
            ><i class="fab fa-linkedin-in icon"></i
          ></a>
        </li>
        <li>
          <a href="https://gabrielescarparo.wordpress.com/" target="blank"
            ><i class="fa-brands fa-wordpress-simple icon" target="blank"></i
          ></a>
        </li>
        <li>
          <a href="https://github.com/gabrisca" target="blank"
            ><i class="fa-brands fa-github icon" target="blank"></i
          ></a>
        </li>
      </ul>
    </div>
    <div class="footer_bottom">
      <div class="site">
        <img src="../assets/img/logob.jpg" alt="logo gabriele scarparo" />
        <span class="name"> gabrielescarparo </span>
        <span class="powered">
          powered by
          <a
            href="https://www.linkedin.com/in/gabriele-scarparo/"
            target="_blank"
          >
            gabrisca
          </a>
        </span>
      </div>
    </div>
  </footer>
</template>

<script>
export default {
  // preparo Footer per essere esportato
  name: "FooterView",
  data() {
    return {
      showFooter: false,
      lastScrollPosition: 0,
    };
  },
  mounted() {
    window.addEventListener("scroll", this.onScroll);
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.onScroll);
  },
  methods: {
    // funzione che nasconde il menu allo scrollDown e lo fa riapparire allo scrollUp (solo sopra width 800)
    onScroll() {
      var viewport_width = window.innerWidth;
      if (viewport_width > 800) {
        // prendo il valore corrente di currentScrollPosition
        const currentScrollPosition = window.pageYOffset;
        if (currentScrollPosition < 10) {
          return;
        }
        // Stoppa l'esecuzione della funzione se la differenza tra currentScrollPosition e lastScrollPosition Ã¨ minore dell'offset (10)
        if (Math.abs(currentScrollPosition - this.lastScrollPosition) < 0) {
          return;
        }
        this.showFooter = currentScrollPosition < this.lastScrollPosition;
        // Setto il valore di currentScrollPosition con il valore di lastScrollPosition
        this.lastScrollPosition = currentScrollPosition;
      } else {
        this.showFooter = false;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
