<template>
  <navbar class="mynav">
    <b-navbar toggleable="lg" type="light" variant="info" fixed="top">
      <b-navbar-brand href="#">
        <router-link class="gs_brand" :to="this.links[0].url">
          <img src="../assets/img/logow.jpg" alt="logo gabrisca" />
          <span>Gabriele</span>
          <span>Scarparo</span>
        </router-link>
      </b-navbar-brand>

      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

      <b-collapse id="nav-collapse" v-model="showCollapse" is-nav>
        <!-- Right aligned nav items -->
        <b-navbar-nav class="ml-auto">
          <b-navbar-nav>
            <ul class="inline_menu">
              <li
                v-for="(link, index) in links"
                :key="index"
                @click.stop=""
                :class="{ active: link.current }"
              >
                <router-link :to="link.url">
                  {{ link.text }}
                </router-link>
              </li>
            </ul>
          </b-navbar-nav>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
  </navbar>
</template>
<script>

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "HeaderView",
  data() {
    return {
      showCollapse: false,
      links: [
        {
          text: "home",
          url: "/",
          current: false,
        },
        {
          text: "about",
          url: "/about",
          current: false,
        },
        {
          text: "projects",
          url: "/projects",
          current: false,
        },
      ],
    };
  },
  watch: {
    $route() {
      // This will close the collapse if any part of the route changes
      // including query, params, hash, name, or path
      this.showCollapse = false;
    },
  },
};
</script>
<style lang="scss" scoped>
@import "../assets/style/app.scss";
.gs_brand {
  img {
    height: 30px;
    margin-right: 5px;
    transition: all 0.6s ease-in-out;
    &:hover {
      transform: scale(1.1);
    }
  }
  span {
    @media all and (max-width: 400px) {
      display: none;
    }
  }
  :nth-child(2) {
    transition: all 0.6s ease-in-out;
    color: $hover-color;
  }
  :nth-child(3) {
    transition: all 0.6s ease-in-out;
    color: $color-secondary;
  }
  &:hover {
    :nth-child(2) {
      color: $color-secondary;
    }
    :nth-child(3) {
      color: $hover-color;
    }
  }
}
#nav-collapse {
  @media all and (min-width: 992px) {
    display: flex !important;
    flex-basis: auto;
    flex-direction: row-reverse;
  }
}
ul {
  li {
    display: inline-block;
    margin-right: 15px;
    text-transform: uppercase;
    text-align: center;
    transition: all 0.6s ease-in-out;
    &:hover {
      transform: scale(1.1);
    }
    @media all and (max-width: 990px) {
      display: block;
      margin: 0;
      margin-bottom: 10px;
      transition: all 0.6s ease-in-out;
      &:hover {
        background: $hover-color;
      }
    }
    a {
      letter-spacing: 1.2px;
      color: $color-secondary;
      &::after {
        height: 1px;
        width: 100%;
        display: block;
        content: "";
        border-bottom: 2px solid $hover-color;
        transform: scaleX(0);
        transition: transform 250ms ease-in-out;
      }
      &:hover {
        text-decoration: none;
        color: $hover-color;
        &::after {
          transform: scaleX(1);
        }
      }
      @media all and (max-width: 992px) {
        &:hover {
          color: white;
        }
      }
    }
  }
  &.social {
    @media all and (min-width: 991px) {
      display: none;
    }
  }
}
// ? OVERRIDE BOOTSTRAP CLASSES
.bg-info {
  //height: 50px;
  background-color: $brand-color !important;
  box-shadow: 0px 0px 8px rgba(4, 4, 4, 0.6);
  padding: 5px 20px 5px 20px;
}
a {
  color: $color-primary;
  text-decoration: none;
}
a.router-link-exact-active {
  color: $hover-color;
  text-decoration: none;
}
a.router-link-active.gs_brand {
  color: $color-primary;
  text-decoration: none;
  text-transform: uppercase;
}
.navbar-light {
  z-index: 998;
  padding: 0px 200px 0px 200px;
  @media all and (max-width: 990px) {
      padding: 5px 10px 5px 10px;
    }
  .navbar-toggler {
    color: rgba(255, 255, 255, 0.5);
    border-color: $color-primary;
    transition: all 0.5s ease;
    &:hover {
      background: $hover-color;
    }
  }
}
</style>
